
h1 { 
    font-size: 2em;
    font-weight: bold;
    color: rgb(98, 22, 117);
    text-align: center;
    margin: 0 0 0.5em 0;
    padding: 0;
}

h2 { 
    font-size: 1.5em;
    font-weight: bold;
    color: rgb(95, 187, 59);
    text-align: center;
    margin: 0 0 0.5em 0;
    padding: 0;
}

p {
    font-size: 1em;
    font-weight: normal;
    color: rgb(0, 0, 0);
    text-align: left;
    column-count: 3;
    column-gap: 1em;
    column-rule: 1px solid rgb(0, 0, 0);   
    margin: 0 0 0.5em 0;
    padding: 0;
}

body {
    width: 100vw;
    height: 100vh;
    margin: 0;
    background-color: blue;
}

.stage {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
    background-color: rgb(255, 255, 255);
}


